import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
import HelloWorld from '@/components/HelloWorld.vue'; // @ is an alias to /src
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        // 声明周期钩子
        this.model = { details: '', title: '' };
        this.fields = [
            {
                type: 'input',
                modelKey: 'title',
                props: { placeholder: '请输入公告标题' },
                label: '公告标题',
                rules: { required: true },
                messages: {
                    required: '请输入公告内容'
                }
            },
            {
                type: 'textarea',
                modelKey: 'details',
                props: { placeholder: '请输入公告详情...', maxlength: '500', autoExpand: true, autofocus: true },
                rules: { required: true },
                messages: {
                    required: '请输入公告内容'
                }
            }
        ];
        this.accountId = '';
        // tab切换配置
        this.selectedLabelDefault = 'notice';
        this.tabBarList = [{
                label: '公告发布',
                value: 'notice'
            }, {
                label: '发布记录',
                value: 'noticeList'
            }];
        // 
        this.page = 1;
        this.isShowMore = true;
        this.accountList = [];
        this.noticeList = [];
    }
    activated() {
        // this.getWeiXinConfig()
        this.pageInit();
    }
    validateHandler(result) {
        this.validity = result.validity;
        this.valid = result.valid;
        console.log(result);
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    getBindingInfo() {
        let query = this.$route.query;
        if (query.code) {
            const toast = this['$createToast']({
                time: 0,
                txt: '数据获取中...'
            });
            toast.show();
            this.$store.dispatch('getCodeInfo', { code: query.code }).then((response) => {
                if (response.success) {
                    if (response.data.openid) {
                        this.openid = response.data.openid;
                        this.$router.replace({ path: '/login', query: { openid: response.data.openid, jump: '/home' } });
                    }
                    else if (response.data.sessionId) {
                        this.$store.commit('SET_SESSION_ID', response.data.sessionId);
                        this.$store.commit('SET_USER_INFO', response.data.userInfo);
                        this.getAdminAccountList();
                    }
                }
                toast.hide();
            });
        }
        else {
            console.log('不是微信登陆');
        }
    }
    pageInit() {
        let query = this.$route.query;
        if (this.$store.state.sessionId && !this.accountId) {
            this.getAdminAccountList();
        }
        else if (query.code && !this.$store.state.sessionId) {
            this.getBindingInfo();
        }
    }
    getAdminAccountList() {
        var that = this;
        this.$store.dispatch('getAdminAccountList', {}).then((response) => {
            if (response.data.list) {
                response.data.list.forEach((item, index) => {
                    that.$set(that.accountList, index, item);
                });
                if (!this.accountId) {
                    this.accountId = this.accountList[0].id;
                }
            }
        });
    }
    tabBarChange(value) {
        if (value === 'noticeList') {
            this.page = 1;
            this.noticeList = [];
            this.getNoticeList();
        }
    }
    submitHandler(e) {
        e.preventDefault();
        let query = {};
        query.accountId = this.accountId;
        query.details = this.model.details;
        query.title = this.model.title;
        this.$store.dispatch('addNotice', query).then((response) => {
            if (response.success) {
                this.model = { details: '', title: '' };
                this.selectedLabelDefault = 'noticeList';
                this.tabBarChange('noticeList');
                this['$createDialog']({
                    type: 'alert',
                    content: '公告发布成功！',
                    icon: 'cubeic-ok'
                }).show();
            }
        });
    }
    get accountOptions() {
        this.accountList.forEach((obj) => {
            obj.value = obj.id;
            obj.text = obj.name;
        });
        return this.accountList;
    }
    get userInfo() {
        return this.$store.state.userInfo;
    }
    getHouseInfo() {
        let info = {};
        this.$store.state.houseList.forEach((obj) => {
            if (obj.id === this.houseId) {
                info = obj;
            }
        });
        return info;
    }
    getNoticeList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getNoticeList', { page: this.page, accountId: this.accountId }).then((response) => {
            if (response.success) {
                if (response.data.list.length > 0) {
                    this.noticeList = this.noticeList.concat(response.data.list);
                }
                else {
                    this.isShowMore = false;
                }
            }
            toast.hide();
        });
    }
    showMoreClick() {
        this.page++;
        this.getNoticeList();
    }
};
__decorate([
    Prop({ default: 'default value' })
], Home.prototype, "propB", void 0);
Home = __decorate([
    Component({
        components: {
            HelloWorld
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            },
            getRepairType(type) {
                if (type === 'type1') {
                    return '开锁服务';
                }
                else if (type === 'type2') {
                    return '公共报修';
                }
                else if (type === 'type3') {
                    return '个人报修';
                }
                else {
                    return '其他报修';
                }
            }
        }
    })
], Home);
export default Home;
